.feedback {
  &__container {
    padding-top: 75px;
    margin-left: 80px;
    background: linear-gradient(112.21deg, #1f1f1f 2.72%, #121212 101.59%);
    display: grid;
    grid-template-columns: 1fr minmax(200px, 540px);
    grid-template-rows: 100vh;
    align-items: start;
    overflow: hidden;
  }

  &__desc {
    padding-top: 130px;
  }

  &__form {
    padding: 95px 50px;
    margin-top: 77px;
    margin-bottom: 78px;
    background: #232323;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.15);
    display: grid;
    grid-template-columns: 1fr;
  }
}
.desc-feedback {
  &__title {
    padding-bottom: 20px;
    font-weight: 700;
    font-size: 60px;
    line-height: 100%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
  }

  &__subtitle {
    padding-bottom: 57px;
    font-weight: 300;
    font-size: 36px;
    line-height: 100%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
  }

  &__columns {
    display: flex;
    margin-right: 86px;
  }

  &__column {
    max-width: 220px;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include mr(56px);
  }
}
.column-feedback {
  &__img {
    margin-bottom: 34px;
    width: 164px;
    height: 164px;
    background: url("../img/feedback-fon.png") no-repeat center;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__text {
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.05em;
    color: rgba(255, 255, 255, 0.55);
  }
  &__title {
    padding-bottom: 16px;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 300;
    font-size: 30px;
    line-height: 100%;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
  }
}
.form-feedback {
  &__subtitle {
    padding-bottom: 15px;
    text-align: center;
    font-weight: 300;
    font-size: 24px;
    line-height: 100%;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
  }

  &__title {
    padding-bottom: 39px;
    text-align: center;
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
  }

  &__label {
    display: flex;
    flex-direction: column-reverse;
    padding-bottom: 35px;
  }

  &__input {
    width: 100%;
    height: 60px;
    margin-top: 5px;
    padding: 21px 19px 15px 22px;
    background-color: rgba(0, 0, 0, 0.2);
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 100%;
    letter-spacing: 0.05em;
    color: rgba(255, 255, 255, 0.5);
    &::placeholder{
      color: #b6b6b6;
      opacity: 0.2;
    }
  }

  &__btn {
    margin-bottom: 40px;
    padding: 21px 0px 19px 0px;
    background-color: #ffffff;
    font-weight: 700;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    text-align: center;
    color: #1b1b1b;
  }

  &__text {
    font-style: normal;
    font-weight: 250;
    font-size: 14px;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.05em;
    color: #ffffff;
    opacity: 0.4;
  }
}
