.model {
  &__container {
    height: 100vh;
    margin-left: 80px;
    background: url(../img/page2-fon.png) no-repeat right bottom,
    linear-gradient(112.21deg, #1f1f1f 2.72%, #121212 101.59%);
    overflow: auto;
  }

  &__swiper {
    padding-right: 80px;
    padding-top: 139px;
    position: relative;
  }

}
.swiper-wrapper {
  overflow: hidden;
}

.slider-item {
  display: grid;
  grid-template-columns: 1fr min-content max-content;
  grid-template-rows: min-content 1fr;
  align-items: start;
  background-color: #232323;
  &__title {
    padding-top: 37px;
    padding-left: 40px;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 100%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
  }
  &_icon {
    padding-top: 37px;
    width: 33px;
    height: 34px;
    fill: none;
    stroke: #fff;
  }

  &__btn {
    grid-column: 2;
    grid-row: 1/3;
    background-color: transparent;
    border: none;
    margin-right: 44px;
    cursor: pointer;
  }

  &__description {

    grid-row: 1/3;
    grid-column: 3;
  }
  &__img {
    grid-row: 2;
    grid-column: 1;
    width: 100%;
    object-fit: cover;
  }
}
.description {
  &__item {
    display: flex;
    align-items: center;
    padding: 33px 68px;
    border-left: 2px solid rgba(196, 196, 196, 0.05);
    border-bottom: 2px solid rgba(196, 196, 196, 0.05);
  }

  &__icon {
    width: 32px;
    height: 32px;
    stroke: #919191;
    margin-right: 10px;
  }

  &__text {
    font-weight: 250;
    font-size: 18px;
    line-height: 100%;
    letter-spacing: 0.05em;
    color: rgba(255, 255, 255, 0.5);
  }

  &__link {
    display: flex;
    align-items: center;
    padding: 41px 61px;
    font-weight: 700;
    font-size: 20px;
    line-height: 100%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
    background: #292929;
  }
}
.swiper-pagination {
  transform: rotate(90deg);
}
.swiper-horizontal{
  & .swiper-pagination-bullets{
    width: 248px;
    height: 37px;
    right: -90px;
    left: auto;
    bottom: 45%;
}
}

.swiper-horizontal{
  & .model__pagination.swiper-pagination-bullets .swiper-pagination-bullet
  {
   margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 15px);
 }
}
.swiper-pagination-bullet {
  width: 24px;
  height: 24px;
  transform: rotate(270deg);
  font-weight: 250;
  font-size: 24px;
  line-height: 100%;
  letter-spacing: 0.185em;
  color: #353535;
}
.swiper-pagination-bullet-active {
  background: none;
  color: #ffffff;
  opacity: 0.5;
}
.swiper-button-next {
  top: 90%;
  &:after {
    color: rgba(255, 255, 255, 0.5);
  }
}
.swiper-button-prev {
  right: 10px;
  left: auto;
  top: 33%;
  &:after {
    color: rgba(255, 255, 255, 0.5);
  }
}

.item-model {
    padding-top: 66px;
  &__title {
    font-size: 56px;
    line-height: 100%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
  }

  &__text {
    font-weight: 250;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #ffffff;
  }

  &__btn {
    margin-top: 33px;
    margin-right: 40px;
    padding: 21px 75px 19px 75px;
    font-weight: 700;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #1b1b1b;
    cursor: pointer;
  }

  &__link {
    background-color: transparent;
    padding: 21px 107px 19px 107px;
    font-weight: 700;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
    border: 1px solid #FFFFFF;
    cursor: pointer;

  }
}
