.model3d {
  &__container {
    padding-top: 83px;
    margin-left: 80px;
    background: linear-gradient(112.21deg, #1f1f1f 2.72%, #121212 101.59%);
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 100vh;
    align-items: center;
    overflow: hidden;
  }
  &__inner {
    margin-left: -233px;
    margin-right: 30px;
    background: url(../img/Круги.svg) no-repeat center;
    position: relative;
    & .tooltip1{
      position: absolute;
      top: 82px;
      left: 558px;
    }
    & .tooltip2{
      position: absolute;
      bottom: 194px;
      left: 399px;
    }
    & .tooltip3{
      position: absolute;
      top: 247px;
      right: 145px;
    }
    & .tooltip__btn {
      width: 46px;
      height: 46px;
      margin-right: 10px;
      border-radius: 50%;
      border: none;
      background-color: #ffffff;
      font-family: "Muller";
      font-style: normal;
      font-weight: 300;
      font-size: 40px;
      line-height: 100%;
      color: #1b1b1b;
      text-align: center;
      box-shadow: 0 0 3px 10px rgba($color: #ffffff, $alpha: .15);
      &:hover + .tooltip__txt{
        opacity: 1;
        transition: opacity 0.3s ease-in-out;
      }
    }
    & .tooltip__txt{
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
      padding: 2px 5px;
      background-color: #ffffff;
      font-family: "Muller";
      font-style: normal;
      font-weight: 300;
      font-size: 24px;
      line-height: 100%;
      color: #1b1b1b;
      text-align: center;
    }
  }

  &__swiper {
    padding-right: 170px;
  }

  &__swiper-wrapper {
    height: 597px;
  }

  &__slide {
    position: relative;
    display: flex;
  }

  &__description {
    flex: 1 1 auto;
    padding-top: 51px;
  }

  &__pagination.swiper-pagination.swiper__pagination {
    left: auto;
    right: 0%;
    top: 300px;
    width: 118px;
    z-index: 2;
  }
  &__pagination.swiper-pagination .swiper-pagination-bullet {
    position: relative;
    margin-bottom: 50px;
    margin-right: 30px;
    width: max-content;
    height: max-content;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 100%;
    text-align: right;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
  }
  &__pagination.swiper-pagination .swiper-pagination-bullet-active-main {
    color: #fff;
    opacity: 1;
    &:after {
      content: "";
      display: inline-block;
      position: absolute;
      right: -27px;
      top: -4px;
      width: 7px;
      height: 40px;
      background-color: #ffffff;
    }
    &__pagination.swiper-pagination .swiper-pagination-bullet-active-prev {
      font-size: 18px;
      line-height: 100%;
      text-align: right;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      color: rgba(255, 255, 255, 0.3);
    }
    &__pagination.swiper-pagination .swiper-pagination-bullet-active-next {
      font-size: 18px;
      line-height: 100%;
      text-align: right;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      color: rgba(255, 255, 255, 0.3);
    }
    &__pagination.swiper-pagination .swiper-pagination-bullet-active-next-next{
      transform: scale(0.66);
    }
  }
}

.description {
  &__swiper-wrapper {
    width: 522px;
    padding-top: 50px;
  }

  &__swiper-pagination {
    font-size: 24px;
    line-height: 100%;
    letter-spacing: 0.185em;
    color: #353535;
    transform: none;
    & .swiper-pagination-current {
      font-weight: 250;
      font-size: 24px;
      line-height: 100%;
      letter-spacing: 0.185em;
      color: #ffffff;
      opacity: 0.5;
    }
    & .swiper-pagination-total {
      font-weight: 250;
      font-size: 24px;
      line-height: 100%;
      letter-spacing: 0.185em;
      color: #353535;
    }
  }

  &__swiper-button-next {
    position: absolute;
    right: 70%;
    top: 7%;
    &:after {
      font-size: calc(var(--swiper-navigation-size) / 1.69);
    }
  }

  &__swiper-button-prev {
    position: absolute;
    right: 95%;
    top: 7%;
    &:after {
      font-size: calc(var(--swiper-navigation-size) / 1.69);
    }
  }
}
.swiper-horizontal {
  & .swiper-pagination-fraction.description__swiper-pagination {
    width: 130px;
    left: 4%;
    top: 4%;
  }
  &
    .description__swiper-pagination.swiper-pagination-fraction
    .swiper-pagination-bullet {
    margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 5px);
  }
}

.slide-description {
  &__title {
    padding-bottom: 19px;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 700;
    font-size: 60px;
    line-height: 100%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
    white-space: nowrap;
  }

  &__text {
    padding-bottom: 22px;
    font-family: "Muller";
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 30px;
    letter-spacing: 0.05em;
    color: rgba(255, 255, 255, 0.55);
  }

  &__link {
    display: inline-block;
    padding: 21px 50px 19px 50px;
    font-weight: 700;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #1b1b1b;
    background-color: #ffffff;
    &:last-child {
      margin-left: 32px;
      padding: 21px 29px 19px 29px;
      color: #ffffff;
      background-color: transparent;
      border: 1px solid #ffffff;
      margin-bottom: 1px;
    }
  }
}

.slide-model3d {
  &__img {
  }

  &__desc {
  }
}

.desc-slide {
  &__item {
    margin-top: 40px;
  }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 60px;
    line-height: 100%;
    letter-spacing: 0.05em;
    color: #ffffff;
  }

  &__subtitle {
    font-style: normal;
    font-weight: 300;
    font-size: 30px;
    line-height: 100%;
    letter-spacing: 0.05em;
    color: #ffffff;
  }
}
