.main-page {
  &__container {
    height: 100vh;
    margin-left: 80px;
    background: linear-gradient(
        112.21deg,
        rgba(31, 31, 31, 0.91) 2.72%,
        rgba(18, 18, 18, 0.91) 101.59%
      ),
      url(../img/page1-fon.jpg);
    @include bg-cover;
  }

  &__wrap {
    display: grid;
    grid-template-columns: 1fr min-content;
    grid-template-rows: 100vh;
  }

  &__item {
    align-self: center;
    width: 775px;
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 72px;
    line-height: 100%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
  }
  &__subtitle{
    font-size: 72px;
  }

  &__text {
    font-weight: 250;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: 0.05em;
    color: #ffffff;
  }

  &__btn {
    margin-top: 27px;
    padding: 21px 76px 19px 76px;
    font-weight: 700;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
    background-color: transparent;
    border: 2px solid #FFFFFF;
  }

  &__social {
    list-style: none;
    padding-bottom: 25px;
    align-self: flex-end;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.social {
  &__item {
    padding-bottom: 30px;

  }

  &__vert {
    width: 1.5px;
    fill: #c4c4c4;
  }

  &__link {
  }

  &__icon {
    width: 34px;
    height: 34px;
    fill: none;
    stroke: #c4c4c4;
  }
}
