.about {
  &__container {
    padding-top: 83px;
    margin-left: 80px;
    background: linear-gradient(112.21deg, #1f1f1f 2.72%, #121212 101.59%);
    display: grid;
    grid-template-columns: minmax(200px, 666px) 1fr;
    grid-template-rows: 100vh;
    align-items: start;
    overflow: hidden;
  }

  &__desc {
    padding-top: 153px;
    padding-left: 70px;
  }

  &__subtitle {
    font-style: normal;
    font-weight: 300;
    font-size: 36px;
    line-height: 100%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
  }

  &__title {
    padding-bottom: 25px;
    font-style: normal;
    font-weight: 700;
    font-size: 60px;
    line-height: 100%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
  }

  &__txt {
    padding-bottom: 50px;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 30px;
    letter-spacing: 0.05em;
    color: rgba(255, 255, 255, 0.55);
  }

  &__columns {
    display: flex;
    padding-bottom: 50px;
  }

  &__column {
    max-width: 127px;
    margin-right: 50px;
  }

  &__link {
    font-weight: 700;
    font-size: 20px;
    line-height: 100%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
  }

  &__icon {
    margin-left: 10px;
    width: 27px;
    height: 13px;
    fill: #c4c4c4;
  }

  &__play {
    position: relative;
    padding-top: 80px;
    height: 100%;
    background: url(../img/about-fon3.png) no-repeat center,
      url(../img/about-fon2.png) no-repeat center,
      url(../img/about-fon1.png) no-repeat center;
  }
}

.column-about {
  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 34px;
    line-height: 100%;
    letter-spacing: 0.05em;
    color: #ffffff;
  }

  &__subtitle {
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 26px;
    letter-spacing: 0.05em;
    color: rgba(255, 255, 255, 0.55);
  }
}
.play-about {
  position: relative;
  &__btn {
    position: absolute;
    bottom: 321px;
    left: 464px;
    width: 90px;
    height: 90px;
    background: url(../img/play.png) no-repeat center;
    background-size: contain;
    border: none;
  }

  &__icon-pointer {
    position: absolute;
    width: 20px;
    height: 20px;
    fill: #3e3e3e;
    &_left {
      top: 23%;
      left: 35%;
    }

    &_right {
      top: 31%;
      right: 22%;
    }

    &_light {
      bottom: 37%;
    left: 19.5%;
      fill: #ffffff;
    }
  }
  &__icon-txt{
    position: absolute;
    bottom: 37%;
    left: -3%;
    font-weight: 300;
    font-size: 18px;
    line-height: 100%;
    letter-spacing: 0.05em;
    color: #ffffff;

  }
}
