.allmodels {
  &__container {
    display: grid;
    grid-template-columns: repeat(5,min-content);
    grid-auto-rows: min-content;
    gap: 40px 45px;
    height: 100vh;
    margin-left: 80px;
    background: linear-gradient(112.21deg, #1f1f1f 2.72%, #121212 101.59%);
    padding-top: 106px;
  }
}
.card {
  width: 300px;
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #232323;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.15);
  transition: 0.2s ease-in-out;
  &:hover{
    transform: scale(1.1);
    transition: 0.2s ease-in-out;
  }
  &__title {
    padding: 26px 0px 0px 0px;
    text-align: center;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    letter-spacing: 0.05em;
    color: #ffffff;
  }

  &__desc {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: min-content min-content;
  }
  &__img{
    opacity: 0.4;
    transition: 0.2s ease-in-out;
    &:hover{
      opacity: 1;
      transition: 0.2s ease-in-out;
    }
  }
}
.desc {
  &__item {
    display: flex;
    align-items: center;
    padding: 8px 20px 6px 20px;
    border: 2px solid #292929;
    &:last-child{
        background-color: #292929;
    }
  }

  &__icon {
    width: 25px;
    height: 25px;
    margin-right: 7px;
    fill: #919191;
  }

  &__text {
    font-weight: 250;
    font-size: 16px;
    line-height: 100%;
    letter-spacing: 0.05em;
    color: rgba(255, 255, 255, 0.5);
  }

  &__link {
    font-weight: 700;
    font-size: 14px;
    line-height: 100%;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
  }
}
