.left-menu {
  &__container {
    position: fixed;
    left: 0;
    z-index: 10;
    height: 100vh;
    width: 80px;
    background: linear-gradient(112.21deg, #1f1f1f 2.72%, #121212 101.59%);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    transition: left 0.3s ease-in-out;
  }

  &__text {
    padding-left: 135px;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.05em;
    color: rgba(255, 255, 255, 0.5);
    pointer-events: none;
    transform: rotate(-90deg);
    white-space: nowrap;
  }
  &__btn-dw {
    background-color: transparent;
    border: none;
    cursor: pointer;
  }
  &__icon {
    width: 23px;
    height: 49.5px;
    fill: #c4c4c4;
    margin-bottom: 50px;
    margin-top: 15px;
  }
  &__burger {
    flex: 1 1 auto;
  }
}
.burger {
  @include burger;
  margin: 42px 19px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
}
.burger--active {
  left: 529px;
  transition: left 0.3s ease-in-out;
}

.burger-menu {
  &__wrap {
    width: 619px;
    position: absolute;
    left: -999px;
    height: 100vh;
    z-index: 3;
    padding: 42px 15px 51px 64px;
    background: linear-gradient(112.21deg, #1f1f1f 2.72%, #121212 101.59%);
    box-shadow: 10px 0px 20px rgba(0, 0, 0, 0.25);
    transition: left 0.3s ease-in-out;
  }

  &__logo {
    padding-bottom: 42px;
  }

  &__items {
    list-style: none;
    border-bottom: 1px solid rgba(196, 196, 196, 0.15);
    margin-bottom: 40px;
  }

  &__item {
    padding-bottom: 40px;
  }

  &__link {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 100%;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #ffffff;
    text-decoration: none;
    &:hover {
      text-shadow: 0px 0px 7px #ffffff;
    }
  }

  &__contacts {
    list-style: none;
  }

  &__contact {
    padding-bottom: 15px;
  }

  &__contact-link {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 100%;
    letter-spacing: 0.1em;
    color: rgba(255, 255, 255, 0.5);
    &:hover {
      color: rgba(255, 255, 255, 0.8);
    }
  }
}

.menu--active {
    left: 0px;
    transition: left 0.3s ease-in-out;
}
