.header {
  &__container {
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 35px;
    margin-left: 80px;
    z-index: 2;
  }

  &__logo {
    width: 227px;
    height: 44px;
  }

  &__menu {
    display: flex;
    justify-content: space-between;
    list-style: none;
    padding: 0px 90px;
  }

  &__items {
    display: flex;
    justify-content: space-between;
  }
}

.menu-header {
  li {
    &:not(:last-of-type) {
      margin-right: 60px;
    }
  }
  &__link {
    white-space: nowrap;
    color: var(--opLight);
    font-weight: 400;
    font-size: 15px;
    line-height: 15px;
    text-align: center;
    letter-spacing: 0.05em;
    &_active{
      color: #ffffff;
      text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.7);
    }
  }
}
.items-header {
  &__lang {
    display: flex;
    align-items: center;

  }

  &__eng {
    padding-right: 12px;
    border-right: 1px solid rgba(255, 255, 255, 0.4);
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.4);
  }

  &__ru {
    padding-left: 12px;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.4);
    &_active {
      color: #ffffff;
      text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.7);
    }
  }

  &__icon {
    width: 34px;
    height: 34px;
    margin-left: 53px;
    stroke: var(--light-color);
  }
}
